@import "https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;700;900&display=swap";
@import "https://fonts.googleapis.com/css2?family=Cardo&display=swap";

*, :before, :after {
  box-sizing: border-box;
}

ul[class], ol[class] {
  padding: 0;
}

body, h1, h2, h3, h4, h5, h6, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

ul, ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input, button, textarea, select {
  font: inherit;
}

body {
  color: #fff;
  font-family: Raleway, sans-serif;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  width: 100%;
  height: 101px;
  z-index: 2;
  background-color: #0000;
  position: absolute;
  top: 0;
  left: 0;
}

.header__wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__nav ul {
  max-width: 670px;
  align-items: center;
  display: flex;
}

.header__nav ul li {
  width: 134px;
  text-align: center;
}

.header__nav ul li a {
  color: #fff;
  font-size: 13px;
  font-weight: 700;
  line-height: 15px;
  text-decoration: none;
}

.header__nav ul li a:active {
  color: #e71414;
}

.header__nav ul li:hover {
  height: 100%;
  border-bottom: 3px solid #babbc5;
  padding: 38px 0;
  display: block;
}

.header__nav ul li:active {
  height: 100%;
  border-bottom: 3px solid #e71414;
  padding: 38px 0;
  display: block;
}

.header span.divider {
  width: 100%;
  border: 1px solid #babbc5;
  display: block;
  position: absolute;
  bottom: 0;
}

.header__burger {
  width: 24px;
  height: 18px;
  display: none;
}

.header__burger button {
  width: 100%;
  height: 100%;
  cursor: pointer;
  background: none;
  border: none;
  flex-direction: column;
  justify-content: space-between;
  padding: 0;
  transition: all .3s ease-in-out;
  display: flex;
}

.header__burger button span {
  width: 100%;
  height: 3px;
  background: #fff;
}

.header__burger button:hover {
  transform: scale(1.1);
}

.header .sidebar {
  height: 556px;
  background: none;
  transition: all .8s ease-in-out;
  display: none;
  position: absolute;
  left: 0;
  transform: translateX(-100%);
}

.header .sidebar__nav-list {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header .sidebar__nav-list li {
  padding: 24px 0;
}

.header .sidebar__nav-list li a {
  color: #fff;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 700;
  line-height: 17px;
  text-decoration: none;
}

.header .sidebar__nav-list li a:hover {
  color: gray;
  cursor: pointer;
}

.header .sidebar__nav-list li a:active {
  color: #e71414;
}

.header .sidebar.sidebar_active {
  transform: translateX(0);
}

.header__burger.header__burger_active span:nth-child(1) {
  transform-origin: 0 0;
  transform: translate(2px, -2px)rotate(45deg);
}

.header__burger.header__burger_active span:nth-child(2) {
  transform-origin: 100% 100%;
  transform: translate(-5px, -10px)rotate(-45deg);
}

.header__burger.header__burger_active span:nth-child(3) {
  transform: scale(0);
}

@media screen and (max-width: 1024px) {
  .header {
    height: 101px;
  }

  .header__logo img {
    max-width: 231px;
    height: 101px;
  }

  .header__nav ul li {
    max-width: 112px;
  }

  .header__nav ul li a {
